
import { defineComponent } from "vue";
import { modalController } from "@ionic/vue";
import { BiillerFeeList, FeeInfoByMethodTypeCode, PAY20700112_REQ, PAY20700112_RES } from "@/shared/type/PAY20700112.type";
import { PAY20700121_REQ, PAY20700121_RES } from "@/shared/type/PAY20700121.type";
import { PAY20700131_REQ, PAY20700131_RES } from "@/shared/type/PAY20700131.type";
import { PAY20700132_REQ, PAY20700132_RES } from "@/shared/type/PAY20700132.type";
import { screenMode } from "@/shared/type/Approval.type";
import { APM20200113_RES } from "@/shared/type/APM20200113.type";
import { ModifyCardType } from "@/shared/components/ModifyCard/ModifyCard.type";
import { FileSize } from "@/shared/services/FileSizeService";
import "@vuepic/vue-datepicker/dist/main.css";
import ModifyCard from "@/shared/components/ModifyCard/ModifyCard.vue";
import BreadCrumb from "@/shared/components/BreadCrumb/BreadCrumb.vue";
import VueDatePicker from "@vuepic/vue-datepicker";
import CurrencyInput from "@/shared/components/CurrencyInput/CurrencyInput.vue";
import SelectOption from '@/shared/components/SelectOption/SelectOption.vue';
import { FILE_TYPE } from "@/shared/constants/common.const";

const fileSize = new FileSize()

export default defineComponent({
    name: "PAY207001SC01",

    components: {
        VueDatePicker,
        ModifyCard,
        BreadCrumb,
        CurrencyInput,
        SelectOption
    },

    data() {
        return {
            headers: [
                { columnName: this.$t('pay207001sc01.no') },
                { columnName: this.$t('pay207001sc01.fee_code') },
                { columnName: this.$t('Fee Calc Method Type Code') },
                { columnName: this.$t('Fee Calc Apply Base Code') },
                { columnName: this.$t('Biller Fee Kind Code') },
                { columnName: this.$t('pay207001sc01.fee_currency_code') },
                { columnName: this.$t('pay207001sc01.apply_start_date') },
                { columnName: this.$t('pay207001sc01.apply_end_date') },
                { columnName: this.$t('pay207001sc01.range_start_amount') },
                { columnName: this.$t('pay207001sc01.range_end_amount') },
                { columnName: this.$t('pay207001sc01.handle_fee') },
                { columnName: this.$t('pay207001sc01.min_fee_amount') },
                { columnName: this.$t('pay207001sc01.max_fee_amount') },
                { columnName: this.$t('pay207001sc01.fee_status') },
            ],
            billerDetail: {
                billerNo: "",
                billerCode: "",
                eBankTransactionTypeCode: "",
                vendorNo: "",
                vendorName: "",
                billerNameEn: "",
                billerNameKh: "",
                customerNo: "",
                customerName: "",
                billerStatusCode: "",
                billerCategoryCode: "",
                billerMCC: "",
                billerAddress: "",
                aggregatorTypeCode: "",
                apiAccessTypeCode: "",
                paymentByTypeCode: "",
                paymentByDescription: "",
                billerPreCheckTypeCode: "",
                settlementAccountTypeCode: "",
                billerUSDAccountNo: "",
                billerKHRAccountNo: "",
                feeChargeFromTypeCode: "",
                activateDate: new Date(),
                remark: "",
                billerImageId: "",
            },
            fileInfo: {
                image: '',
                imgSize: '',
                imageName: ''
            },
            modifyCard: {} as ModifyCardType,
            feeInfo: {
                productFeeCode: "",
                feeCalcMethodTypeCode: "",
                currencyCode: "",
                statusCode: "",
                applyStartDate: new Date(),
                applyEndDate: new Date(),
                rangeStartAmount: "0",
                rangeEndAmount: "0",
                handleFee: "0",
                feeMinAmount: "0",
                feeMaxAmount: "0",
                applySeqNo: 0,
                billerFeeKindCode: '',
                feeCalcApplyBaseCode: '',
            },
            feeCurrencyCode: '',
            productFeeCodeEdit: '',
            tempProductFeeCode: {} as FeeInfoByMethodTypeCode,
            billerFeeList: [] as BiillerFeeList[],
            productFeeCodes: [] as FeeInfoByMethodTypeCode[],
            selectProductFeeCode: {} as FeeInfoByMethodTypeCode,
            productFeeCodesSelect: {} as { key: string, value: string },
            isEditable: false as boolean,
            isInValidInput: false as boolean,
            getImage: null,
            isInvalidMinFeeAmount: false as boolean,
            isInvalidRangeStartAmount: false as boolean,
            isInValidFeeInfo: false as boolean,
            isFeeEdit: false as boolean,
            isInvalidId: false as boolean,
            isApproval: false as boolean,
            isImageRemove: false,
            isEditleFeeInfo: true,
            operationMode: 'normal' as screenMode,
            approvalRes: {} as any
        }
    },

    computed: {
        title(): string {
            return this.$route.query.type == 'register' ? 'Biller Register' : 'Biller Edit'
        },
        approvalType(): string {
            return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : ''
        },

        formattedDate(): string {
            return this.$datetime.formatVueDatePicker(this.billerDetail.activateDate);
        },

        applyStartDate(): string {
            return this.$datetime.formatVueDatePicker(this.feeInfo.applyStartDate);
        },

        applyEndDate(): string {
            return this.$datetime.formatVueDatePicker(this.feeInfo.applyEndDate);
        },

        billerStatusCode(): any {
            return this.$store.state.CommonCodeModule.commonCode.BillerStatusCode;
        },

        billerCategoryCode(): any {
            return this.$store.state.CommonCodeModule.commonCode.BillerCategoryCode;
        },

        billerMccCode(): any {
            return this.$store.state.CommonCodeModule.commonCode.BillerMCC;
        },

        billerPreCheckTypeCode(): any {
            return this.$store.state.CommonCodeModule.commonCode.BillerPreCheckTypeCode;
        },

        apiAccessTypeCode(): any {
            return this.$store.state.CommonCodeModule.commonCode.APIAccessTypeCode;
        },

        paymentByTypeCode(): any {
            return this.$store.state.CommonCodeModule.commonCode.PaymentByTypeCode;
        },

        settlementAccountTypeCode(): any {
            return this.$store.state.CommonCodeModule.commonCode.SettlementAccountTypeCode;
        },

        feeChargeFromTypeCode(): any {
            return this.$store.state.CommonCodeModule.commonCode.FeeChargeFromTypeCode;
        },

        aggregatorTypeCode(): any {
            return this.$store.state.CommonCodeModule.commonCode.AggregatorTypeCode;
        },

        FeeTypeCode(): any {
            return this.$store.state.CommonCodeModule.commonCode.FeeCalcMethodTypeCode;
        },

        FeeCalcApplyBaseCode(): any {
            return this.$store.state.CommonCodeModule.commonCode.FeeCalcApplyBaseCode
        },

        BillerFeeKindCode(): any {
            return this.$store.state.CommonCodeModule.commonCode.BillerFeeKindCode
        },

        FeeStatusCode(): any {
            return this.$store.state.CommonCodeModule.commonCode.FeeStatusCode;
        },

        btnCancel(): boolean {
            if (this.$route.query.type === "register" || this.isEditable) {
                return true;
            }
            return false;
        },
        btnDelete(): boolean {
            if (this.$route.query.type === "edit" && !this.isEditable) {
                return true;
            }
            return false;
        },
        btnList(): boolean {
            if (this.$route.query.type === "edit" && !this.isEditable) {
                return true;
            }
            return false;
        },
        btnEdit(): boolean {
            if (!this.isEditable) {
                return true;
            }
            return false;
        },
        btnSave(): boolean {
            if (this.$route.query.type === "register" || this.isEditable) {
                return true;
            }
            return false;
        },

        btnAdd(): boolean {
            const isThereTrue = this.billerFeeList.find((item) => item.isChecked)
            if (this.isEditable && isThereTrue) {
                return true;
            }
            return false;
        },

        btnRemove(): boolean {
            const isThereTrue = this.billerFeeList.find((item) => item.isChecked)
            if (!isThereTrue) {
                return true;
            }
            return false;
        },

        btnClear(): boolean {
            const isThereTrue = this.billerFeeList.find((item) => item.isChecked)
            if (isThereTrue && this.isEditable) {
                return true;
            }
            return false;
        },

        btnSearch(): boolean {
            if (this.isEditable && !this.billerDetail.customerNo) {
                return true;
            }
            return false;
        },

        btnFeeEdit() {
            const isThereTrue = this.billerFeeList.find((item) => item.isChecked)
            if (this.isEditable && !this.isFeeEdit && !isThereTrue) {
                return true
            }
            return false
        },

        btnFeeSave() {
            const isThereTrue = this.billerFeeList.find((item) => item.isChecked)
            if (this.isEditable && this.isFeeEdit && !isThereTrue) {
                return true
            }
            return false
        },

        btnFeeCancel() {
            if (this.isEditable && this.isFeeEdit) {
                return true
            }
            return false
        },

        invalidMinFeeAmount(): boolean {
            if (Number(this.feeInfo.feeMinAmount) > Number(this.feeInfo.feeMaxAmount)) {
                return true
            }
            return false
        },

        invalidRangeStartAmount(): boolean {
            if (Number(this.feeInfo.rangeStartAmount) > Number(this.feeInfo.rangeEndAmount)) {
                return true;
            }
            return false;
        },

        isAmount(): boolean {
            if (this.feeInfo.feeCalcApplyBaseCode === '01') {
                return true
            }
            return false
        },

        isFeeCalcMethodTytpeCode(): boolean {
            if (this.feeInfo.feeCalcMethodTypeCode === '01') {
                return true
            }
            return false
        }
    },

    async mounted() {
        if (this.approvalType === '') {
            await this.requestFeeCalcMethodTypeCode()
            if (this.$route.query.type === "register") {
                this.isEditable = true
            } else {
                this.requestBillerDetail()
            }
        } else {
            await this.requestFeeCalcMethodTypeCode()
            await this.$approval.getAuthorizationDetail().then(async (authDetail: APM20200113_RES) => {
                let authDetailParsed = JSON.parse(authDetail.body.requestContent)
                this.approvalRes = authDetail.body
                this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal'
                this.isApproval = true
                if (this.$util.checkServiceIdType(authDetail.body.serviceID) == 'delete') {
                    const reqBody = {
                        billerNo: authDetailParsed.billerNo,
                        billerCode: authDetailParsed.billerCode
                    } as PAY20700112_REQ;
                    const res = await this.$request.getRequest("PAY20700112", reqBody, false) as PAY20700112_RES
                    this.billerDetail = {
                        billerNo: res?.body.billerNo,
                        billerCode: res?.body.billerCode,
                        eBankTransactionTypeCode: res?.body.eBankTransactionTypeCode,
                        vendorNo: res?.body.vendorNo,
                        vendorName: res?.body.vendorName,
                        billerNameEn: res?.body.billerNameEn,
                        billerNameKh: res?.body.billerNameKh,
                        customerNo: res?.body.customerNo,
                        customerName: res?.body.customerName,
                        billerStatusCode: res?.body.billerStatusCode,
                        billerCategoryCode: res?.body.billerCategoryCode,
                        billerMCC: res?.body.billerMCC,
                        billerAddress: res?.body.billerAddress,
                        aggregatorTypeCode: res?.body.aggregatorTypeCode,
                        apiAccessTypeCode: res?.body.apiAccessTypeCode,
                        paymentByTypeCode: res?.body.paymentByTypeCode,
                        paymentByDescription: res?.body.paymentByDescription,
                        billerPreCheckTypeCode: res?.body.billerPreCheckTypeCode,
                        settlementAccountTypeCode: res?.body.settlementAccountTypeCode,
                        billerUSDAccountNo: res?.body.billerUSDAccountNo,
                        billerKHRAccountNo: res?.body.billerKHRAccountNo,
                        feeChargeFromTypeCode: res?.body.feeChargeFromTypeCode,
                        activateDate: this.$datetime.convertStringToDate(res?.body.activateDate),
                        remark: res?.body.remark,
                        billerImageId: res?.body.billerImageId
                    }
                    // request table
                    this.billerFeeList = res.body.billerFeeList.map((item: BiillerFeeList) => {
                        return {
                            productFeeCode: item.productFeeCode,
                            currencyCode: item.currencyCode,
                            feeCalcMethodTypeCode: item.feeCalcMethodTypeCode,
                            applyStartDate: item.applyStartDate,
                            applyEndDate: item.applyEndDate,
                            rangeStartAmount: item.rangeStartAmount,
                            rangeEndAmount: item.rangeEndAmount,
                            handleFee: item.handleFee,
                            feeMinAmount: item.feeMinAmount,
                            feeMaxAmount: item.feeMaxAmount,
                            statusCode: `${item.statusCode} : ${this.$codeConverter.convertValue("FeeStatusCode", item.statusCode)}`,
                            applySeqNo: item.applySeqNo,
                            billerFeeKindCode: item.billerFeeKindCode,
                            feeCalcApplyBaseCode: item.feeCalcApplyBaseCode
                        }
                    })
                    //Get File Image Info
                    if (this.billerDetail.billerImageId && this.billerDetail.billerImageId.length > 6) {
                        const resImageInfo = await this.$file.getFileInfo(this.billerDetail.billerImageId)
                        this.fileInfo.imageName = resImageInfo?.data.body.originalFileName
                        this.fileInfo.imgSize = resImageInfo?.data.body.fileSizeString
                        const url = await this.$file.previewImage(this.billerDetail.billerImageId)
                        this.fileInfo.image = url
                    }
                } else {
                    this.billerDetail = {
                        billerNo: authDetailParsed.billerNo,
                        billerCode: authDetailParsed.billerCode,
                        eBankTransactionTypeCode: authDetailParsed.eBankTransactionTypeCode,
                        vendorNo: authDetailParsed.vendorNo,
                        vendorName: authDetailParsed.vendorName,
                        billerNameEn: authDetailParsed.billerNameEn,
                        billerNameKh: authDetailParsed.billerNameKh,
                        customerNo: authDetailParsed.customerNo,
                        customerName: authDetailParsed.customerName,
                        billerStatusCode: authDetailParsed.billerStatusCode,
                        billerCategoryCode: authDetailParsed.billerCategoryCode,
                        billerMCC: authDetailParsed.billerMCC,
                        billerAddress: authDetailParsed.billerAddress,
                        aggregatorTypeCode: authDetailParsed.aggregatorTypeCode,
                        apiAccessTypeCode: authDetailParsed.apiAccessTypeCode,
                        paymentByTypeCode: authDetailParsed.paymentByTypeCode,
                        paymentByDescription: authDetailParsed.paymentByDescription,
                        billerPreCheckTypeCode: authDetailParsed.billerPreCheckTypeCode,
                        settlementAccountTypeCode: authDetailParsed.settlementAccountTypeCode,
                        billerUSDAccountNo: authDetailParsed.billerUSDAccountNo,
                        billerKHRAccountNo: authDetailParsed.billerKHRAccountNo,
                        feeChargeFromTypeCode: authDetailParsed.feeChargeFromTypeCode,
                        activateDate: this.$datetime.convertStringToDate(authDetailParsed.activateDate),
                        remark: authDetailParsed.remark,
                        billerImageId: authDetailParsed.billerImageId
                    }
                    // request table
                    this.billerFeeList = authDetailParsed.billerFeeList.map((item: BiillerFeeList) => {
                        return {
                            productFeeCode: item.productFeeCode,
                            currencyCode: item.currencyCode,
                            feeCalcMethodTypeCode: item.feeCalcMethodTypeCode,
                            applyStartDate: item.applyStartDate,
                            applyEndDate: item.applyEndDate,
                            rangeStartAmount: item.rangeStartAmount,
                            rangeEndAmount: item.rangeEndAmount,
                            handleFee: item.handleFee,
                            feeMinAmount: item.feeMinAmount,
                            feeMaxAmount: item.feeMaxAmount,
                            statusCode: `${item.statusCode} : ${this.$codeConverter.convertValue("FeeStatusCode", item.statusCode)}`,
                            applySeqNo: item.applySeqNo
                        }
                    })
                    //Get File Image Info
                    if (this.billerDetail.billerImageId && this.billerDetail.billerImageId.length > 6) {
                        const resImageInfo = await this.$file.getFileInfo(this.billerDetail.billerImageId)
                        this.fileInfo.imageName = resImageInfo?.data.body.originalFileName
                        this.fileInfo.imgSize = resImageInfo?.data.body.fileSizeString
                        const url = await this.$file.previewImage(this.billerDetail.billerImageId)
                        this.fileInfo.image = url
                    }
                }
            })
        }
    },


    watch: {
        tempProductFeeCode: {
            handler(newFeeInfo: FeeInfoByMethodTypeCode) {
                this.productFeeCodeEdit = `${newFeeInfo.productFeeCode} : ${newFeeInfo.feeName}` || ''
                this.feeInfo.productFeeCode = newFeeInfo.productFeeCode
                this.feeInfo.currencyCode = newFeeInfo.feeCurrencyCode
            },
            deep: true
        },

        feeInfo: {
            handler(newVal) {
                if (Number(newVal.feeMinAmount) > Number(newVal.feeMaxAmount)) {
                    this.isInvalidMinFeeAmount = true;
                } else {
                    this.isInvalidMinFeeAmount = false;
                }
                if (Number(newVal.rangeStartAmount) > Number(newVal.rangeEndAmount)) {
                    this.isInvalidRangeStartAmount = true
                } else {
                    this.isInvalidRangeStartAmount = false
                }
            },
            deep: true,
        },

        'billerDetail.customerNo': function (customerNo: string) {
            // this.customerData.customerNo = customerNo.replace(' ', '')
            if (customerNo.trim().length == 7 || customerNo.trim().length == 8) {
                this.requestCustomerInformation()
            } else {
                this.billerDetail.customerName = ''
            }
        }

        // billerFeeList: {
        //     handler(list) {
        //         const isChecked = list.some((obj: BiillerFeeList) => obj.isChecked)
        //         this.isEditleFeeInfo = isChecked ? false : true
        //     },
        //     deep: true
        // }
    },

    methods: {
        async requestFeeCalcMethodTypeCode() {
            const reqBody = { feeCalcMethodTypeCode: "" }
            const res: any = await this.$request.getRequest("PFM20100113", reqBody, false)
            if (res.header.result) {
                this.productFeeCodes = res.body.list
            }
        },

        async requestCustomerInformation() {
            const reqBody = { customerNo: this.billerDetail.customerNo };
            const res: any = await this.$request.getRequest("CIB20100114", reqBody, false, undefined, false, false)
            if (res?.header?.result) {
                this.billerDetail.customerName = res.body.customerFullName
                this.isInvalidId = false
            } else {
                this.isInvalidId = true
            }
        },

        async requestBillerDetail() {
            const reqBody = {
                billerNo: this.$route.query.billerNo,
                billerCode: this.$route.query.billerCode
            } as PAY20700112_REQ;
            const res = await this.$request.getRequest("PAY20700112", reqBody, false) as PAY20700112_RES
            if (res?.header.result) {
                this.billerDetail = {
                    billerNo: res?.body.billerNo,
                    billerCode: res?.body.billerCode,
                    eBankTransactionTypeCode: res?.body.eBankTransactionTypeCode,
                    vendorNo: res?.body.vendorNo,
                    vendorName: res?.body.vendorName,
                    billerNameEn: res?.body.billerNameEn,
                    billerNameKh: res?.body.billerNameKh,
                    customerNo: res?.body.customerNo,
                    customerName: res?.body.customerName,
                    billerStatusCode: res?.body.billerStatusCode,
                    billerCategoryCode: res?.body.billerCategoryCode,
                    billerMCC: res?.body.billerMCC,
                    billerAddress: res?.body.billerAddress,
                    aggregatorTypeCode: res?.body.aggregatorTypeCode,
                    apiAccessTypeCode: res?.body.apiAccessTypeCode,
                    paymentByTypeCode: res?.body.paymentByTypeCode,
                    paymentByDescription: res?.body.paymentByDescription,
                    billerPreCheckTypeCode: res?.body.billerPreCheckTypeCode,
                    settlementAccountTypeCode: res?.body.settlementAccountTypeCode,
                    billerUSDAccountNo: res?.body.billerUSDAccountNo,
                    billerKHRAccountNo: res?.body.billerKHRAccountNo,
                    feeChargeFromTypeCode: res?.body.feeChargeFromTypeCode,
                    activateDate: this.$datetime.convertStringToDate(res?.body.activateDate),
                    remark: res?.body.remark,
                    billerImageId: res?.body.billerImageId
                }
                // request table
                this.billerFeeList = res.body.billerFeeList.map((item: BiillerFeeList) => {
                    return {
                        productFeeCode: item.productFeeCode,
                        currencyCode: item.currencyCode,
                        feeCalcMethodTypeCode: item.feeCalcMethodTypeCode,
                        applyStartDate: item.applyStartDate,
                        applyEndDate: item.applyEndDate,
                        rangeStartAmount: item.rangeStartAmount,
                        rangeEndAmount: item.rangeEndAmount,
                        handleFee: item.handleFee,
                        feeMinAmount: item.feeMinAmount,
                        feeMaxAmount: item.feeMaxAmount,
                        statusCode: `${item.statusCode} : ${this.$codeConverter.convertValue("FeeStatusCode", item.statusCode)}`,
                        applySeqNo: item.applySeqNo,
                        isChecked: false,
                        billerFeeKindCode: item.billerFeeKindCode,
                        feeCalcApplyBaseCode: item.feeCalcApplyBaseCode
                    }
                })

                //ModiyCard
                this.modifyCard = {
                    registerTellerId: res?.body.firstRegisterTellerID,
                    registerTellerName: res?.body.firstRegisterTellerName,
                    registerDate: res?.body.firstRegisterDate,
                    registerTime: res?.body.firstRegisterTime,
                    updatedTellerId: this.billerDetail.billerStatusCode === '04' ? '' : res?.body.lastChangeTellerID,
                    updatedTellerName: this.billerDetail.billerStatusCode === '04' ? '' : res?.body.lastChangeTellerName,
                    updatedDate: this.billerDetail.billerStatusCode === '04' ? '' : res?.body.lastChangeDate,
                    updatedTime: this.billerDetail.billerStatusCode === '04' ? '' : res?.body.lastChangeTime,
                    deleteTellerId: this.billerDetail.billerStatusCode === '04' ? res?.body.lastChangeTellerID : '',
                    deleteTellerName: this.billerDetail.billerStatusCode === '04' ? res?.body.lastChangeTellerName : '',
                    deleteDate: this.billerDetail.billerStatusCode === '04' ? res?.body.lastChangeDate : '',
                    deleteTime: this.billerDetail.billerStatusCode === '04' ? res?.body.lastChangeTime : '',
                }
                //Get File Image Info
                if (this.billerDetail.billerImageId && this.billerDetail.billerImageId.length > 6) {
                    const resImageInfo = await this.$file.getFileInfo(this.billerDetail.billerImageId)
                    this.fileInfo.imageName = resImageInfo?.data.body.originalFileName
                    this.fileInfo.imgSize = resImageInfo?.data.body.fileSizeString
                    const url = await this.$file.previewImage(this.billerDetail.billerImageId)
                    this.fileInfo.image = url
                }
            }
        },

        async handleEdit() {
            if (this.billerDetail.billerImageId.length > 9 && this.getImage !== null && !this.isImageRemove) {
                const res = await this.$file.uploadFile([this.getImage], "ICM")
                this.$file.updateFileStatus(this.billerDetail.billerImageId, FILE_TYPE.INACTIVE);
                for (let i = 0; i < res.data.body.fileList.length; i++) {
                    this.billerDetail.billerImageId = res.data.body.fileList[i].fileId
                }
            } else if (this.billerDetail.billerImageId.length < 9 && this.getImage !== null && !this.isImageRemove) {
                const res = await this.$file.uploadFile([this.getImage], "ICM")
                for (let i = 0; i < res.data.body.fileList.length; i++) {
                    this.billerDetail.billerImageId = res.data.body.fileList[i].fileId
                }
            } else if (this.isImageRemove) {
                await this.$file.updateFileStatus(this.billerDetail.billerImageId, FILE_TYPE.INACTIVE);
                this.billerDetail.billerImageId = ''
            }
            const feeList = this.billerFeeList.map((item: BiillerFeeList, index: number) => {
                return {
                    productFeeCode: item.productFeeCode,
                    feeCalcMethodTypeCode: item.feeCalcMethodTypeCode,
                    currencyCode: item.currencyCode,
                    statusCode: item.statusCode.split(" : ")[0],
                    applyStartDate: item.applyStartDate,
                    applyEndDate: item.applyEndDate,
                    rangeStartAmount: Number(item.rangeStartAmount),
                    rangeEndAmount: Number(item.rangeEndAmount),
                    handleFee: Number(item.handleFee),
                    feeMinAmount: Number(item.feeMinAmount),
                    feeMaxAmount: Number(item.feeMaxAmount),
                    applySeqNo: index,
                    billerFeeKindCode: item.billerFeeKindCode,
                    feeCalcApplyBaseCode: item.feeCalcApplyBaseCode
                }
            })

            const reqBody = {
                billerNo: this.billerDetail.billerNo,
                billerCode: this.billerDetail.billerCode,
                vendorNo: this.billerDetail.vendorNo,
                vendorName: this.billerDetail.vendorName,
                billerNameEn: this.billerDetail.billerNameEn,
                billerNameKh: this.billerDetail.billerNameKh,
                customerNo: this.billerDetail.customerNo,
                customerName: this.billerDetail.customerName,
                billerStatusCode: this.billerDetail.billerStatusCode,
                billerCategoryCode: this.billerDetail.billerCategoryCode,
                billerMCC: this.billerDetail.billerMCC,
                billerAddress: this.billerDetail.billerAddress,
                aggregatorTypeCode: this.billerDetail.aggregatorTypeCode,
                apiAccessTypeCode: this.billerDetail.apiAccessTypeCode,
                paymentByTypeCode: this.billerDetail.paymentByTypeCode,
                paymentByDescription: this.billerDetail.paymentByDescription,
                billerPreCheckTypeCode: this.billerDetail.billerPreCheckTypeCode,
                settlementAccountTypeCode: this.billerDetail.settlementAccountTypeCode,
                billerUSDAccountNo: this.billerDetail.billerUSDAccountNo,
                billerKHRAccountNo: this.billerDetail.billerKHRAccountNo,
                feeChargeFromTypeCode: this.billerDetail.feeChargeFromTypeCode,
                activateDate: this.$datetime.convertDateToString(this.billerDetail.activateDate),
                remark: this.billerDetail.remark,
                billerImageId: this.billerDetail.billerImageId,
                billerFeeList: feeList
            } as PAY20700131_REQ;

            const res = await this.$request.getRequest("PAY20700131", reqBody, false) as PAY20700131_RES
            if (res?.header?.result) {
                this.$dialog.onOpen({
                    content: this.$t('message.update_success'),
                    dialogMode: "Success",
                    callback: () => {
                        this.requestBillerDetail()
                        modalController.dismiss()
                    }
                })
            }
        },

        async handleRegister() {
            if (this.getImage !== null) {
                const res = await this.$file.uploadFile([this.getImage], "ICM");
                for (let i = 0; i < res.data.body.fileList.length; i++) {
                    this.billerDetail.billerImageId = res.data.body.fileList[i].fileId
                }
            }
            const feeList = this.billerFeeList.map((item) => {
                return {
                    productFeeCode: item.productFeeCode,
                    feeCalcMethodTypeCode: item.feeCalcMethodTypeCode,
                    currencyCode: item.currencyCode,
                    statusCode: item.statusCode.split(" : ")[0],
                    applyStartDate: item.applyStartDate,
                    applyEndDate: item.applyEndDate,
                    rangeStartAmount: Number(item.rangeStartAmount),
                    rangeEndAmount: Number(item.rangeEndAmount),
                    handleFee: Number(item.handleFee),
                    feeMinAmount: Number(item.feeMinAmount),
                    feeMaxAmount: Number(item.feeMaxAmount),
                    billerFeeKindCode: item.billerFeeKindCode,
                    feeCalcApplyBaseCode: item.feeCalcApplyBaseCode
                }
            })

            const reqBody = {
                billerCode: this.billerDetail.billerCode,
                vendorNo: this.billerDetail.vendorNo,
                vendorName: this.billerDetail.vendorName,
                billerNameEn: this.billerDetail.billerNameEn,
                billerNameKh: this.billerDetail.billerNameKh,
                customerNo: this.billerDetail.customerNo,
                customerName: this.billerDetail.customerName,
                billerStatusCode: this.billerDetail.billerStatusCode,
                billerCategoryCode: this.billerDetail.billerCategoryCode,
                billerMCC: this.billerDetail.billerMCC,
                billerAddress: this.billerDetail.billerAddress,
                aggregatorTypeCode: this.billerDetail.aggregatorTypeCode,
                apiAccessTypeCode: this.billerDetail.apiAccessTypeCode,
                paymentByTypeCode: this.billerDetail.paymentByTypeCode,
                paymentByDescription: this.billerDetail.paymentByDescription,
                billerPreCheckTypeCode: this.billerDetail.billerPreCheckTypeCode,
                settlementAccountTypeCode: this.billerDetail.settlementAccountTypeCode,
                billerUSDAccountNo: this.billerDetail.billerUSDAccountNo,
                billerKHRAccountNo: this.billerDetail.billerKHRAccountNo,
                feeChargeFromTypeCode: this.billerDetail.feeChargeFromTypeCode,
                activateDate: this.$datetime.convertDateToString(this.billerDetail.activateDate),
                remark: this.billerDetail.remark,
                billerImageId: this.billerDetail.billerImageId,
                eBankTransactionTypeCode: this.billerDetail.eBankTransactionTypeCode,
                billerFeeList: feeList
            } as PAY20700121_REQ;
            const res = await this.$request.getRequest("PAY20700121", reqBody, false) as PAY20700121_RES
            if (res?.header?.result) {
                this.$router.push("PAY207001SC00")
                this.$dialog.onOpen({
                    content: this.$t('message.register_success'),
                    dialogMode: "Success",
                    callback: () => {
                        modalController.dismiss()
                    }
                })
            }
        },

        async handleDelete() {
            const reqBody = {
                billerNo: this.billerDetail.billerNo,
                billerCode: this.billerDetail.billerCode
            } as PAY20700132_REQ;
            const res = await this.$request.getRequest("PAY20700132", reqBody, false) as PAY20700132_RES
            if (res?.header?.result) {
                this.$router.push("PAY207001SC00")
                this.$dialog.onOpen({
                    content: this.$t('message.delete_success'),
                    dialogMode: "Success",
                    callback: () => {
                        modalController.dismiss()
                    }
                })
            }
        },

        handleUploadImage(event: any) {
            this.isImageRemove = false
            const accectFile = /(\.png)$/i;
            const files = event.target.files;
            const file = files[0];
            if (!accectFile.exec(file.name)) {
                this.$dialog.onOpen({
                    content: this.$t('message.only_png_file_can_be_inserted'),
                    dialogMode: "Warning",
                    callback: () => {
                        modalController.dismiss();
                    },
                });
            } else if (file.size > 1000000) {
                this.$dialog.onOpen({
                    content: this.$t('message.file_size_must_not_exceed_1MB'),
                    dialogMode: "Warning",
                    callback: () => {
                        modalController.dismiss();
                    },
                });
            } else {
                this.getImage = file;
                const size = fileSize.countFileSize(file.size);
                this.fileInfo.imgSize = size;
                const reader = new FileReader();
                reader.readAsDataURL(file);
                this.fileInfo.imageName = file.name;
                reader.onload = () => {
                    this.fileInfo.image = String(reader.result);
                };
            }
        },

        statusConverter(statusCode: string): string {
            switch (statusCode) {
                case '01 : Active':
                    return 'success'
                case '02 : Inactive':
                    return 'fail'
                default:
                    return 'info'
            }
        },

        handleCheckbox(_, indx: number) {
            this.billerFeeList.map((item, index) => {
                if (indx === index) {
                    item.isChecked = !item.isChecked
                } else {
                    item.isChecked = false
                }
            })
            this.convertFromTableToFeeInfo()
        },

        handleFeeAdd() {
            const formatValue = {
                productFeeCode: this.tempProductFeeCode.productFeeCode,
                currencyCode: this.tempProductFeeCode.feeCurrencyCode,
                feeCalcMethodTypeCode: this.feeInfo.feeCalcMethodTypeCode,
                applyStartDate: this.$datetime.convertDateToString(this.feeInfo.applyStartDate),
                applyEndDate: this.$datetime.convertDateToString(this.feeInfo.applyEndDate),
                rangeStartAmount: this.feeInfo.rangeStartAmount,
                rangeEndAmount: this.feeInfo.rangeEndAmount,
                handleFee: this.feeInfo.handleFee,
                feeMinAmount: this.feeInfo.feeMinAmount,
                feeMaxAmount: this.feeInfo.feeMaxAmount,
                statusCode: `${this.feeInfo.statusCode} : ${this.$codeConverter.convertValue("FeeStatusCode", this.feeInfo.statusCode)}`,
                applySeqNo: this.billerFeeList.length,
                isChecked: false,
                billerFeeKindCode: this.feeInfo.billerFeeKindCode,
                feeCalcApplyBaseCode: this.feeInfo.feeCalcApplyBaseCode,
            }
            //add to table
            if (!this.invalidMinFeeAmount && !this.invalidRangeStartAmount && this.tempProductFeeCode && this.feeInfo.feeCalcMethodTypeCode && this.feeInfo.statusCode && this.feeInfo.feeCalcApplyBaseCode && this.feeInfo.billerFeeKindCode) {
                this.billerFeeList.push(JSON.parse(JSON.stringify(formatValue)))
            } else {
                this.isInValidFeeInfo = true
            }
        },

        handleFeeInfoClear() {
            this.isInValidFeeInfo = false
            this.feeInfo.feeCalcMethodTypeCode = ''
            this.feeInfo.feeCalcApplyBaseCode = ''
            this.feeInfo.billerFeeKindCode = ''
            this.feeInfo.applyEndDate = new Date()
            this.feeInfo.applyStartDate = new Date()
            this.feeInfo.rangeStartAmount = "0"
            this.feeInfo.rangeEndAmount = "0"
            this.feeInfo.feeMaxAmount = "0"
            this.feeInfo.feeMinAmount = "0"
            this.feeInfo.handleFee = "0"
        },

        handleFeeInfoCancel() {
            this.isFeeEdit = false
            this.handleFeeInfoClear()
            this.convertFromTableToFeeInfo()
            this.isEditleFeeInfo = false
        },

        handleFeeInfoEdit() {
            this.isFeeEdit = true
            this.isEditleFeeInfo = true
        },

        handleFeeInfoSave() {
            if (!this.invalidMinFeeAmount && !this.invalidRangeStartAmount && this.tempProductFeeCode && this.feeInfo.feeCalcMethodTypeCode && this.feeInfo.statusCode && this.feeInfo.feeCalcApplyBaseCode && this.feeInfo.billerFeeKindCode) {
                const obj = this.billerFeeList.filter(item => item.isChecked)
                Object.assign(obj[0], {
                    productFeeCode: this.feeInfo.productFeeCode,
                    currencyCode: this.feeInfo.currencyCode,
                    feeCalcMethodTypeCode: this.feeInfo.feeCalcMethodTypeCode,
                    applyStartDate: this.$datetime.convertDateToString(this.feeInfo.applyStartDate),
                    applyEndDate: this.$datetime.convertDateToString(this.feeInfo.applyEndDate),
                    rangeStartAmount: this.feeInfo.rangeStartAmount,
                    rangeEndAmount: this.feeInfo.rangeEndAmount,
                    handleFee: this.feeInfo.handleFee,
                    feeMinAmount: this.feeInfo.feeMinAmount,
                    feeMaxAmount: this.feeInfo.feeMaxAmount,
                    statusCode: `${this.feeInfo.statusCode} : ${this.$codeConverter.convertValue('FeeStatusCode', this.feeInfo.statusCode)}`,
                    applySeqNo: this.billerFeeList.length,
                    billerFeeKindCode: this.feeInfo.billerFeeKindCode,
                    feeCalcApplyBaseCode: this.feeInfo.feeCalcApplyBaseCode
                })
                this.isFeeEdit = false
                this.isEditleFeeInfo = false
            } else {
                this.isInValidFeeInfo = true
            }
        },

        handleRemove() {
            this.billerFeeList = this.billerFeeList.filter((item) => !item.isChecked)
            this.billerFeeList.forEach((item, index) => { item.applySeqNo = index })
            this.handleFeeInfoClear()
            this.isEditleFeeInfo = true
        },

        removeFile() {
            this.getImage = null
            this.isImageRemove = true
            this.fileInfo.imageName = ""
            this.fileInfo.imgSize = ""
        },

        convertFromTableToFeeInfo() {
            const obj = this.billerFeeList.filter(item => item.isChecked)
            this.tempProductFeeCode = this.handleFilterFeeSelectCode(obj[0].productFeeCode)
            this.feeInfo = {
                productFeeCode: obj[0].productFeeCode,
                feeCalcMethodTypeCode: obj[0].feeCalcMethodTypeCode,
                currencyCode: obj[0].currencyCode,
                statusCode: obj[0].statusCode.split(" : ")[0],
                applyStartDate: this.$datetime.convertStringToDate(obj[0].applyStartDate),
                applyEndDate: this.$datetime.convertStringToDate(obj[0].applyEndDate),
                rangeStartAmount: obj[0].rangeStartAmount,
                rangeEndAmount: obj[0].rangeEndAmount,
                handleFee: obj[0].handleFee,
                feeMinAmount: obj[0].feeMinAmount,
                feeMaxAmount: obj[0].feeMaxAmount,
                applySeqNo: obj[0].applySeqNo,
                billerFeeKindCode: obj[0].billerFeeKindCode,
                feeCalcApplyBaseCode: obj[0].feeCalcApplyBaseCode,
            }
            this.feeInfo.currencyCode = JSON.parse(JSON.stringify(obj[0].currencyCode))
        },

        handleFilterFeeSelectCode(feeCode) {
            try {
                const filterCode = this.productFeeCodes.find((item) => {
                    return item.productFeeCode === feeCode
                })
                return {
                    feeName: filterCode?.feeName,
                    applyStartDate: filterCode?.applyStartDate,
                    applyEndDate: filterCode?.applyEndDate,
                    feeDescription: filterCode?.feeDescription,
                    productFeeCode: filterCode?.productFeeCode,
                    feeCurrencyCode: filterCode?.feeCurrencyCode,
                    feeShortName: filterCode?.feeShortName,
                    applySeqNo: filterCode?.applySeqNo
                } as FeeInfoByMethodTypeCode
            } catch (error) {
                return {} as FeeInfoByMethodTypeCode
            }
        },

        handleClickAction(actionType: string) {
            switch (actionType) {
                case "delete":
                    this.$dialog.onOpen({
                        content: this.$t("message.are_you_sure_you_want_to_delete"),
                        dialogMode: "Question",
                        callback: () => {
                            this.handleDelete();
                            modalController.dismiss();
                        },
                    });
                    break;
                case "cancel":
                    this.$dialog.onOpen({
                        content: `${this.$t("message.cancel_without_saving_changes")}`,
                        dialogMode: "Question",
                        callback: () => {
                            if (this.$route.query.type == "edit") {
                                this.isEditable = false
                                this.isInvalidId = false
                                this.isFeeEdit = false
                                this.isEditleFeeInfo = true
                                this.isInValidInput = false
                                this.isInValidFeeInfo = false
                                this.requestBillerDetail();
                            } else {
                                this.$router.push("PAY207001SC00");
                            }
                        },
                    });
                    break;
                case "save":
                    var { billerCode, billerNameEn, billerNameKh, customerNo, billerStatusCode, billerCategoryCode, feeChargeFromTypeCode, settlementAccountTypeCode, paymentByTypeCode, billerPreCheckTypeCode, apiAccessTypeCode, aggregatorTypeCode, billerMCC } = this.billerDetail
                    if (billerNameEn && billerNameKh && customerNo && billerStatusCode && billerCategoryCode && billerMCC && aggregatorTypeCode && apiAccessTypeCode && billerPreCheckTypeCode && paymentByTypeCode && settlementAccountTypeCode && feeChargeFromTypeCode && !this.isInvalidId) {
                        this.isInValidFeeInfo = false
                        this.$dialog.onOpen({
                            content: this.$t("message.would_you_like_to_save"),
                            dialogMode: "Question",
                            callback: () => {
                                if (this.$route.query.type === "edit") {
                                    this.handleEdit();
                                    this.isEditable = false
                                } else {
                                    this.handleRegister();
                                }
                            },
                        });
                    } else {
                        this.isInValidInput = true;
                        this.$util.scrollToElement('inp_error')
                    }
                    break;
                case "edit":
                    this.isEditable = true;
                    break;
                case 'list':
                    this.$router.push("PAY207001SC00");
                    break;
            }
        },
    },
});
